import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-color-select',
  templateUrl: './color.component.html',
  styleUrls: ['./color.component.scss']
})
export class ColorSelectComponent implements OnInit {

  public errorColorsNotSelected: boolean = false;
  public showColorDetails: boolean = false;

  @Input() setDefaultValue: boolean = false;

  private _options: Array<any>;
  @Input()
  set options(value: Array<any>) {
    this._options = value.filter(x => x.placas.length > 0);
    if (this.setDefaultValue) {
      this.setColor(this.options[0]);
    }
  }
  get options(): Array<any> {
    return this._options;
  }

  @Output() selected = new EventEmitter();

  choosenColor: any;

  constructor(private sanitizer: DomSanitizer) { }

  ngOnInit() {
  }

  showDetails() {
    this.showColorDetails = true;
  }

  hideDetails() {
    this.showColorDetails = false;
  }

  getColorBg(color) {
    const bgStyle = `linear-gradient(180deg, ${color.Hexadecimal} 50%, ${color.Hexadecimal} 50%)`;
    return this.sanitizer.bypassSecurityTrustStyle(bgStyle);
  }

  getTitle(color) {
    return color.pintura_tipo && color.pintura_tipo.name ? `${color.Cor} ${color.pintura_tipo.name}` : color.Cor;
  }

  setColor(color) {
    if (color !== this.choosenColor) {
      this.choosenColor = color;
      this.selected.emit(color);
    }
  }
}
