import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AsyncValidator, FormControl, ValidationErrors } from '@angular/forms';
import { catchError, map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import { API_VALIDATE_CONDUCTOR } from 'src/app/core/api/app.api';

@Injectable({
  providedIn: 'root'
})
export class ValidatorConductorService implements AsyncValidator {

  constructor(
    private http: HttpClient
  ) { }

  validate(field: FormControl): Promise<ValidationErrors | null> | Observable<ValidationErrors | null> {
    const cpf = field.value.replace('\D', '');
    const url = API_VALIDATE_CONDUCTOR + `?cpf=${cpf}`;
    
    if (!cpf || cpf.length < 11) {
      return of(null);
    }

    return this.http.get<any>(url).pipe(
      map(data => !data.success || data.status == 0 ? { conductorNotValid: true } : null),
      catchError(() => of({ conductorNotValid: true }) )
    );
  }
}
