import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { MainSharedService } from 'src/app/util/shared.service';
@Component({
  selector: 'app-period',
  templateUrl: './period.component.html',
  styleUrls: ['./period.component.scss']
})
export class PeriodComponent implements OnInit {

  public errorTermsNotSelected: boolean = false;
  public periodDisabled: boolean = false

  private _options: Array<any>;
  @Input()
  set options(value: Array<any>) {
    this._options = value;
    if (this.setDefaultValue) {
      this.periodDisabled = true
      this.selected.emit(value[0].meses);
      this.checkOptions(value);
    } else {
      this.checkOptions(value);
    }
  }
  get options(): Array<any> {
    return this._options;
  }

  @Input() setDefaultValue: boolean = false
  @Output() selected = new EventEmitter;

  choosenPeriod: any;
  selectedIndex: number;

  constructor(
    public shared: MainSharedService
  ) { }

  ngOnInit() {
  }

  checkOptions(options) {
    if (options.length > 0) {
      this.setPeriod(options[options.length - 1]);
    }
  }

  setPeriodByIndex(index) {
    this.setPeriod(this._options[index]);
  }

  setPeriod(period) {

    this.choosenPeriod = period;

    this.selectedIndex = this.options.findIndex(_period => {
      return _period.meses === period.meses;
    });

    this.selected.emit(period);
  }
}