import { Component, OnInit, Input, Output, EventEmitter, AfterViewInit } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { FormatNumber } from 'src/app/util/formatNumber.util';
import { SkeletonLoaderComponent } from 'src/app/shared/skeleton-loader/skeleton-loader.component';

const openPanel =
  trigger('slideInOut', [
    state('in', style({
      opacity: '1',
      display: 'flex'
    })),
    state('out', style({
      opacity: '0',
      display: 'none'
    })),
    transition('in => out', animate('400ms ease-in-out')),
    transition('out => in', animate('400ms ease-in-out'))
  ]);

@Component({
  selector: 'app-protections',
  templateUrl: './protections.component.html',
  styleUrls: ['./protections.component.scss'],
  animations: [openPanel],
  providers: [SkeletonLoaderComponent]
})
export class ProtectionsComponent implements OnInit, AfterViewInit {

  @Input() isMobile: boolean;
  @Input() options: Array<any>;
  @Input() activePanel;
  @Input() loadingProtections: boolean = true;

  @Output() setPanel = new EventEmitter();
  @Output() selected = new EventEmitter();

  formatNumber: FormatNumber = new FormatNumber();

  public chosenProtection: boolean = false;
  public baseValue = 0;

  constructor() { }

  ngOnInit() { }

  ngAfterViewInit() {
    this.sendDefaultProtection();
  }

  getProtectionCopart(copart) {
    const formattedPrice = copart ? this.formatNumber.getBRLCurrency(Number(copart)) : null;
    return formattedPrice ? `Valor da coparticipao: ${formattedPrice}.` : null;
  }

  panelOpened() {
    this.setPanel.emit('protections');
  }

  // Exibe a seo contendo o painel de mais detalhes de cada proteo exibida
  public toggleDetail(protection) {
    protection.panelDetail = protection.panelDetail === 'out' ? 'in' : 'out';
  }

  // Envia a proteo escolhida para a pgina controladora car-detail-movida 
  sendProtection(protection) {
    this.selected.emit(protection);
  }

  // Envia a proteo default para a pgina controladora car-detail-movida
  sendDefaultProtection() {
    this.options.map((x) => {
      if (x.selected) {
        this.selected.emit(x);
      }
    });
  }
}
