export class CarOptions {
    id: string;
    id_vetor: string;
    sku?: string;
    fipe: string;
    brand: any;
    model: any;
    version: any;
    images: Array<any>;
    color: any;
    plate: any;
    serialItems: {
        absBrake: boolean;
        airBag: boolean;
        airConditioning: boolean;
        doors: string;
        fuel: string;
        gear: string;
    };
    period: any;
    mileage: any;
    tariff: any;
    pickupStore: any;
    protection: any;
    extraProtections: Array<any>;
    additionalServices: Array<any>;
    exclusiveProducts: Array<any>;
    userType?: string;
    groupId?: string;
    acriss?: string;
    sigla: string;
    bookingPayload?: any;
    sellerCode: number;
    values: {
        tariff: string;
        protections: string;
        extraProtections: string;
        additionalServices: string;
        total: string;
        promo?: string;
    };
    lead?: {
        city: string;
        state: string;
        name: string;
        cpf: string;
        phone: string;
        email: string;
    };

    constructor() {
        this.id = '';
        this.id_vetor = '';
        this.brand = {};
        this.model = {};
        this.version = {};
        this.images = [];
        this.color = {};
        this.plate = {};
        this.serialItems = {
            absBrake: true,
            airBag: true,
            airConditioning: true,
            doors: '',
            fuel: '',
            gear: ''
        };
        this.period = {};
        this.mileage = {};
        this.tariff = {};
        this.pickupStore = {};
        this.protection = {};
        this.extraProtections = [];
        this.additionalServices = [];
        this.exclusiveProducts = [];
        this.sellerCode = null;
        this.values = {
            tariff: '0',
            protections: '0',
            extraProtections: '0',
            additionalServices: '0',
            total: '0'
        };
        this.lead = {
            city: '',
            state: '',
            name: '',
            cpf: '',
            phone: '',
            email: ''
        };
    }
}