import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { ValidatorService } from 'src/app/core/services/validator.service';

@Component({
  selector: 'seller-code',
  templateUrl: './seller-code.component.html',
  styleUrls: ['./seller-code.component.scss']
})
export class SellerCodeComponent implements OnInit {

  private _code: string;
  @Input()
  set code(value: string) {
    this._code = value;
    this.sellerCode.emit(value);
  }
  get code(): string {
    return this._code;
  }

  @Output('sellerCodeEmitted') sellerCode = new EventEmitter();

  public formData = new FormGroup({
    sellerCode: new FormControl('')
  });

  constructor(
    private validator: ValidatorService
  ) { }

  ngOnInit() {
  }

  checkNumbers(event) {
    this.validator.checkNumbers(event);
  }

  setSellerCode() {
    const data = this.formData.getRawValue();
    this.sellerCode.emit(data.sellerCode ? data.sellerCode : null);
  }
}
