import {Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges} from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { ValidatorService } from 'src/app/core/services/validator.service';
import { SkeletonLoaderComponent } from 'src/app/shared/skeleton-loader/skeleton-loader.component';
import { ValidatorConductorService } from 'src/app/core/services/validator-conductor.service';

const openPanel =
  trigger('slideInOut', [
    state('in', style({
      opacity: '1',
      display: 'flex'
    })),
    state('out', style({
      opacity: '0',
      display: 'none'
    })),
    transition('in => out', animate('400ms ease-in-out')),
    transition('out => in', animate('400ms ease-in-out'))
  ]);
@Component({
  selector: 'app-additional-services',
  templateUrl: './additional-services.component.html',
  styleUrls: ['./additional-services.component.scss'],
  animations: [openPanel],
  providers: [SkeletonLoaderComponent]
})
export class AdditionalServicesComponent implements OnInit, OnChanges {

  @Input() isMobile: boolean;
  @Input() activePanel;
  @Input() sellerPreSelection;

  private _options: Array<any>;
  @Input()
  set options(services: Array<any>) {
    const addIndex = this.selectedAdditionalServices.map(x => x.sigla).indexOf('ADD');
    if (addIndex > -1) {
      services.map(service => {
        if (service.sigla === 'ADD') {
          service.checked = true;
        }
      });
    }

    this._options = services;
  }
  get options(): Array<any> {
    return this._options;
  }

  @Output() setPanel = new EventEmitter();
  @Output() selected = new EventEmitter();
  @Output() selectedAdditionalDrivers = new EventEmitter();
  @Output() formData = new EventEmitter();
  @Input() loadingAdditionalServices: boolean = true;

  public toggle: boolean = false;
  public selectedAdditionalServices = new Array<any>();
  public additionalDrivers = new Array<any>();

  public conductorsForm = new FormGroup({
    condutor1: new FormControl('',
      [ Validators.required, this.validator.isValidCpf, this.validator.repetitionValidator ],
      this.validatorConductor.validate.bind(this.validatorConductor)
    ),
    condutor2: new FormControl({ value: '', disabled: true },
      [ this.validator.isValidCpf, this.validator.repetitionValidator ],
      this.validatorConductor.validate.bind(this.validatorConductor)
    ),
    condutor3: new FormControl({ value: '', disabled: true },
      [ this.validator.isValidCpf, this.validator.repetitionValidator ],
      this.validatorConductor.validate.bind(this.validatorConductor)
    )
  });

  public chosenAdditionalService: boolean = false;
  public baseValue = 0;

  constructor(
    private validator: ValidatorService,
    private validatorConductor: ValidatorConductorService
  ) { }

  ngOnInit() {
    // Set first cpf as touched
    this.conductorsForm.get('condutor1').markAsTouched();
    // // Forando Sem Parar como adicionado no carregamento para campanha.
    // this.sendAdditionalService({
    //   "id": 215,
    //   "id_vetor": 218,
    //   "price_id": 278,
    //   "sigla": "SPZKM",
    //   "sku": "1",
    //   "nome": "SEM PARAR",
    //   "descricao": "O SEM PARAR  um meio de pagamento automtico para pedgios e estacionamentos",
    //   "valor": "30.00",
    //   "checked": false,
    //   "panelDetail": "out"
    // });
    this.panelOpened();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.hasOwnProperty('activePanel')) {
      this.changeAdditionalSelecteds();
    }
  }

  panelOpened() {
    this.setPanel.emit('addServices');
  }

  // Exibe a seo contendo o painel de mais detalhes de cada servio adicional exibido
  toggleDetail(service) {
    service.panelDetail = service.panelDetail === 'out' ? 'in' : 'out';
  }

  // Exibe a seo contendo o painel com os inputs de CPF dos condutores adicionais
  toggleSection(service) {
    if (service.sigla === 'ADD') {
      this.toggle = !this.toggle;
    }
  }

  // Envia o array de servios adicionais para a pgina controladora car-detail-movida
  sendAdditionalService(additionalService) {
    setTimeout(() => {
      if (this.selectedAdditionalServices.map(x => x.sigla).indexOf(additionalService.sigla) === -1) {
        if (additionalService.sigla === 'ADD') this.formData.emit(this.conductorsForm);
        this.selectedAdditionalServices.push(additionalService);
      } else {
        if (additionalService.sigla === 'ADD') this.formData.emit(undefined);
        this.selectedAdditionalServices.splice(this.selectedAdditionalServices.map(x => x.sigla).indexOf(additionalService.sigla), 1);
      }

      this.selected.emit(this.selectedAdditionalServices);
    }, 200);
  }

  getControlError(controlName: string): string {
    if (!this.conductorsForm.get(controlName).errors) return;
    if (this.conductorsForm.get(controlName).errors.required) return "CPF obrigatrio";
    if (this.conductorsForm.get(controlName).errors.cpfNotValid) return "CPF invlido";
    if (this.conductorsForm.get(controlName).errors.duplicated) return "CPF duplicado";
    if (this.conductorsForm.get(controlName).errors.conductorNotValid) return "CPF do condutor no permitido";
    return;
  }

  // Envia o array de CPF's para a pgina controladora car-detail-movida
  sendAdditionalDriver($event) {
    // If value isn't empty or full, cancel
    if ($event.target.value != null && $event.target.value != '' && $event.target.value.length < 14) return;

    // Getting values
    let values: string[] = []
    Object.keys(this.conductorsForm.controls).forEach(key => {
      let value = this.conductorsForm.get(key).value;
      if (value) values.push(value)
    });

    // Setting values skipping gaps
    Object.keys(this.conductorsForm.controls).forEach((key, index) => {
      if (!values.hasOwnProperty(index)) {
        if (index != 0) this.conductorsForm.get(key).reset()
      }
    });

    // emitting form
    this.formData.emit(this.conductorsForm);

    // Enabling and disabling according to rules
    for (let i = 1; i <= 3; i++) {

      // Geeting current field and fields around
      const prevField = this.conductorsForm.get(`condutor${i - 1}`)
      const field = this.conductorsForm.get(`condutor${i}`)
      const nextField = this.conductorsForm.get(`condutor${i + 1}`)

      // If current is valid, enable next
      if (field.valid && !!nextField) nextField.enable();

      // If current is empty but is not the first, reset
      if (i > 1 && field.value == '') field.reset();

      // If curent and previous is empty, disable
      if (prevField && !prevField.value && !field.value) {
        field.reset()
        field.disable()
      }
    }

    // Getting final values
    this.additionalDrivers = [];
    Object.keys(this.conductorsForm.controls).forEach(key => {
      const field = this.conductorsForm.get(key);
      if (!field.valid) return;
      if (this.additionalDrivers.includes({ cpf: field.value })) return;
      this.additionalDrivers.push({ cpf: field.value });
    });
  }

  emitDrivers() {
    // emitting final values
    if (this.additionalDrivers.length > 0) {
      this.selectedAdditionalDrivers.emit(this.additionalDrivers);
      let additionalDrivers = [];
      Object.keys(this.additionalDrivers).forEach(key => {
        if (this.additionalDrivers[key].cpf) {
          additionalDrivers.push({
            cpf: this.additionalDrivers[key].cpf,
            nome: 'Condutor',
            sobrenome: 'Adicional'
          });
        }
      });
      localStorage.setItem('condutores', JSON.stringify(additionalDrivers));
    }
  }

  changeAdditionalSelecteds() {
    let found: boolean;
    if (typeof this.sellerPreSelection !== 'undefined' && this.sellerPreSelection.hasOwnProperty('servicos_adicionais') && typeof this.sellerPreSelection.servicos_adicionais === 'object') {
      found = false;
      this.sellerPreSelection.servicos_adicionais.forEach((item) => {
        Object.keys(this.selectedAdditionalServices).map( (k) => {
          if (this.selectedAdditionalServices[k].id === item.id) {
            found = true;
          }
        });
        if (!found) {
          this.selectedAdditionalServices.push(item);
        }
      });
      this.selected.emit(this.selectedAdditionalServices);
    }
  }
}
