import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { FormatNumber } from 'src/app/util/formatNumber.util';

const openPanel =
  trigger('slideInOut', [
    state('in', style({
      opacity: '1',
      display: 'flex'
    })),
    state('out', style({
      opacity: '0',
      display: 'none'
    })),
    transition('in => out', animate('400ms ease-in-out')),
    transition('out => in', animate('400ms ease-in-out'))
  ]);

@Component({
  selector: 'app-extra-protections',
  templateUrl: './extra-protections.component.html',
  styleUrls: ['./extra-protections.component.scss'],
  animations: [openPanel]
})
export class ExtraProtectionsComponent implements OnInit {

  @Input() isMobile: boolean;
  @Input() options: Array<any>;
  @Input() activePanel;

  @Output() setPanel = new EventEmitter();
  @Output() selected = new EventEmitter();

  formatNumber: FormatNumber = new FormatNumber();

  public selectedExtraProtections = new Array<any>();
  public chosenExtraProtection: boolean = false;
  public baseValue = 0;

  constructor() { }

  ngOnInit() {
  }

  getProtectionDescription(description, copart) {
    const formattedPrice = copart ? this.formatNumber.getBRLCurrency(Number(copart)) : null;
    return formattedPrice ? `${description} Valor da coparticipao: ${formattedPrice}.` : description;
  }

  panelOpened() {
    this.setPanel.emit('extraProtections');
  }

  // Exibe a seo contendo o painel de mais detalhes de cada proteo extra exibida
  public toggleDetail(extraProtection) {
    extraProtection.panelDetail = extraProtection.panelDetail === 'out' ? 'in' : 'out';
  }

  // Envia o array de protees extras para a pgina controladora car-detail-movida
  sendExtraProtection(extraProtection) {

    this.options.map(item => {
      if (item == extraProtection) return;
      item.selected = false
    })

    setTimeout(() => {
      const selected = this.options.find(item => item.selected)
      if (selected) {
        this.selectedExtraProtections = [selected]
      } else {
        this.selectedExtraProtections = []
      }
      this.selected.emit(this.selectedExtraProtections);
    }, 200);

  }

}
