import { Component, OnInit, Input } from '@angular/core';
import { MediaService } from 'src/app/core/services/media.service';
import { QuotationService } from 'src/app/core/services/quotation.service';

@Component({
  selector: 'app-serial-items',
  templateUrl: './serial-items.component.html',
  styleUrls: ['./serial-items.component.scss']
})
export class SerialItemsComponent implements OnInit {

  public moreInfoUrl: string = "";
  public datasheetUrl: string;
  hasAir: boolean = false;
  hasAirBag: boolean = false;
  hasAbs: boolean = false;
  mediaLoaded: boolean = false;

  @Input() flagLocation = false;

  private _car;
  @Input()
  set car(value) {
    this._car = value;
    this.setConditions(value.acessorios);
    if (!this.flagLocation) this.setDatasheetUrl(this._car);
  }
  get car() {
    return this._car;
  }


  constructor(
    private quotationS: QuotationService,
    private mediaService: MediaService
  ) { }

  ngOnInit() { }

  setConditions(acessorios) {
    if (acessorios && acessorios.length > 0) {
      this.hasAir = this.quotationS.checkInArray('AR CONDICIONADO', this.car.acessorios);
      this.hasAirBag = this.quotationS.checkInArray('AIR BAG', this.car.acessorios);
      this.hasAbs = this.quotationS.checkInArray('ABS', this.car.acessorios);
    }
  }

  private setDatasheetUrl(car: any): void {
    if (car !== undefined && car.CodigoFIPE !== undefined) {
      this.mediaService.getMedia('datasheets-image').subscribe(res => {
        this.mediaLoaded = true;
        const datasheet = res.data.filter(
          res => (res.type === 'pdf' && res.titulo === car.CodigoFIPE)
        );

        if (datasheet.length > 0 && datasheet[0].src !== undefined && datasheet[0].type !== undefined) {
          this.datasheetUrl = datasheet[0].src;
        }
       }, () => (this.mediaLoaded = true));
    }
  }
}
