import { Component, Input, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ValidatorService } from 'src/app/core/services/validator.service';
import { MainSharedService } from 'src/app/util/shared.service';
import { CarDetailService } from 'src/app/core/services/car-detail.service';
import { CarOptions } from '../models/car-options.model';
import { OrderService } from 'src/app/core/services/order.service';
import { AlertMessageComponent } from '../alert-message/alert-message.component';
import { ActivatedRoute, Router } from '@angular/router';
import { EmitEvent, EventBusService, Events } from 'src/app/core/services/eventbus.service';
import { SubSink } from 'subsink';
//import CryptoJS from 'crypto-js'
import { SellerService } from 'src/app/core/services/seller.service';
import { GtmService } from 'src/app/core/services/gtm.service';
import { LocalStorage } from 'src/app/util/local-storage.service';
import { Crypter } from 'src/app/util/cripto.util';

declare let ga: Function;

export type IStatus = 'form' | 'error' | 'success';

@Component({
  selector: 'app-modal-on-demand',
  templateUrl: './modal-on-demand.component.html',
  styleUrls: ['./modal-on-demand.component.scss'],
  providers: [AlertMessageComponent]
})
export class ModalOnDemandComponent implements OnInit, OnDestroy {

  public showModal: boolean = false;
  public status: IStatus = 'form';
  public carSlug: string;
  public month: number;
  public affiliated: any;
  public disableSendLeadButton: boolean = false;

  // Validate form ondemand
  public nomeRequired: boolean = false
  public telefoneRequired: boolean = false
  public cpfRequired: boolean = false
  public emailRequired: boolean = false
  private triggerAlert: boolean = true;

  // String Total Value
  @Input() isPromo: boolean = false;
  @Input() monthPromo: number = null;
  @Input() messagePromo: number = null;
  @Input() promoValue: string;
  @Input() totalValue: string;

  public periods = [];
  public franchises = [];

  // Choosen Car Options
  @Input() carOpt: CarOptions = new CarOptions();

  @Output() showModalOnDemand = new EventEmitter()

  private sub = new SubSink()

  public formData = new FormGroup({
    email: new FormControl(null, [Validators.required, Validators.email]),
    cpf: new FormControl(null, [Validators.required, this.validator.isValidCpf, Validators.minLength(11)]),
    telefone: new FormControl(null, [Validators.required, Validators.minLength(11)]),
    nome: new FormControl(null, Validators.required),
    mensagem: new FormControl(null, [Validators.maxLength(255)])
  });

  private defaultUtm = {
    utm_source: '',
    utm_medium: '',
    utm_campaign: '',
    utm_content: '',
    utm_term: '',
  }

  private $body = document.querySelector('body');
  //private scrollPosition = 0;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    public shared: MainSharedService,
    private eventBusService: EventBusService,
    private sellerS: SellerService,
    private validator: ValidatorService,
    private gtm: GtmService,
    private orderS: OrderService,
    private loadProductS: CarDetailService,
    private alertMsg: AlertMessageComponent,
    private localStorage: LocalStorage,
  ) {
    // Verifica o tamanho da tela e seta numa propriedade scss
    // let vh = window.innerHeight * 0.01;
    // document.documentElement.style.setProperty('--vh', `${vh}px`);
  }

  ngOnInit() {
    this.carSlug = this.route.snapshot.params.model;
  }

  ngOnDestroy() {
    this.sub.unsubscribe()
  }

  sendTariff(e) {
    if (this.showModal) this.eventBusService.emit(new EmitEvent(Events.receiveTariff, e));
  }

  showErrorAlert(msg = null) {
    if (this.triggerAlert) {
      this.shared.setLoadingAtivo(false);
      this.triggerAlert = false;
      const message = msg ? msg : 'No foi possvel carregar as informaes do veculo. Por favor, tente novamente mais tarde.';
      this.alertMsg.openDialog({
        tipo: 'redirect',
        titulo: 'Desculpe,',
        mensagem: message
      }, 'redirect', true);
    }
  }

  async getPeriods() {
    this.loadProductS.getTariffs().subscribe(res => {
      if (res !== null && res !== undefined && res && res.success) {
        this.periods = res.data.sort((a, b) => a.meses - b.meses);
      } else {
        this.showErrorAlert();
      }
    }, ex => {
      this.showErrorAlert();
    });
  }

  async getFranchises(period: number) {
    try {
      const periodFiltered = this.periods.length > 0 ? this.periods.filter(x => x.meses === period) : [];
      this.franchises = periodFiltered.length > 0 ? periodFiltered[0].franquias.sort((a, b) => a.valor - b.valor) : [];
      this.carOpt.sigla = this.franchises[0].sigla
    } catch (ex) {
      this.showErrorAlert();
    }
  }

  selectedPeriod(period) {
    this.month = period.meses
    this.carOpt.period = period;
    this.getFranchises(period.meses);
  }

  selectedAffiliated(affiliated) {
    this.affiliated = affiliated;
  }

  disableScroll() {
    this.$body.style.overflow = 'hidden';
  }

  enableScroll() {
    this.$body.style.removeProperty('overflow');
    window.scrollTo(0, 0);
  }

  checkNumbers(event) {
    this.validator.checkNumbers(event);
  }

  checkLetters(event) {
    this.validator.checkLetters(event);
  }

  toggleStatus(status: IStatus) {
    this.status = status;
  }

  openModal() {
    this.showModal = true;
    this.toggleStatus('form');
    this.disableScroll();
    if (this.showModal) this.getPeriods()
  }

  hideModal() {
    this.showModalOnDemand.emit(false)
    this.formData.reset();
    this.showModal = false;
    this.emailRequired = false
    this.cpfRequired = false
    this.nomeRequired = false
    this.telefoneRequired = false
    this.carOpt.mileage.valor.sigla = undefined
    this.enableScroll();
  }


  sendOnDemand() {
    if (!this.affiliated) {
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Ops! Algo est errado.',
        mensagem: "Selecione uma filial para retirar o veculo"
      }, 'message', true);
      return
    }

    this.disableSendLeadButton = true;

    if (!this.formData.valid) {
      this.formData.controls['cpf'].hasError('required') ? this.cpfRequired = true : this.cpfRequired = false
      this.formData.controls['nome'].hasError('required') ? this.nomeRequired = true : this.nomeRequired = false
      this.formData.controls['telefone'].hasError('required') ? this.telefoneRequired = true : this.telefoneRequired = false
      this.formData.controls['email'].hasError('required') ? this.emailRequired = true : this.emailRequired = false
      this.disableSendLeadButton = false;
      return
    }

    const formData = this.formData.getRawValue();
    const data = {
      'FilialID': this.affiliated.FilialID,
      'filialSigla': this.affiliated.Sigla,
      'email': formData.email,
      'cpf': formData.cpf,
      'telefone': formData.telefone,
      'nome': formData.nome,
      'mensagem': formData.mensagem
    }
    this.shared.onDemand = {
      filialID: data.FilialID,
      filialSigla: data.filialSigla,
      nome: data.nome,
      cpf: data.cpf,
      email: data.email,
      telefone: data.telefone,
      mensagem: data.mensagem
    }
    this.vehicleDetailsOnDemand(this.carOpt);
  }

  vehicleDetailsOnDemand(carDetails: CarOptions) {
    this.shared.setCounterLoading(true);
    this.shared.vehicleDetails = carDetails;
    this.enableScroll();
    if (this.shared.choosenView === 'seller') {
      this.shared.setCounterLoading(false);
      this.lockVehicleAndCreateLink(carDetails);
    } else {
      this.shared.setCounterLoading(false);
      this.createOrderOnDemand()
    }
  }

  lockVehicleAndCreateLink(vehicle) {
    if (this.shared.choosenView === 'seller') {
      const data = {
        vendedor: this.shared.user.PessoaID,
        nome: this.shared.onDemand.nome,
        cpf: this.shared.onDemand.cpf,
        cidade: this.shared.onDemand.cidade,
        estado: this.shared.onDemand.estado,
        email: this.shared.onDemand.email,
        telefone: this.shared.onDemand.telefone,
        mensagem: this.shared.onDemand.mensagem,
        on_demand: true,
        veiculo: {
          nome: this.carSlug,
          fipe: vehicle.fipe
        },
        periodo: {
          meses: vehicle.period.meses,
          franquia: vehicle.mileage.valor.valor,
          sigla: vehicle.mileage.valor.sigla
        },
      }
      const token = Crypter.encrypter(data);
      this.sellerS.createSellerLink(token).subscribe(resToken => {
        this.shared.setCounterLoading(false);
        this.router.navigate([`/vendedor/link/${resToken.data.hash}`]);
        return;
      })
    } else {
      this.sellerS.lockVehicle(vehicle.plate.info.Placa).subscribe(res => {
        if (res.success) {
          const data = {
            vendedor: this.shared.user.PessoaID,
            veiculo: {
              nome: this.carSlug,
              fipe: vehicle.fipe,
              groupId: vehicle.groupId
            },
            loja: {
              FilialID: vehicle.pickupStore.FilialID,
              CEP: vehicle.pickupStore.CEP,
              Loja: vehicle.pickupStore.Loja,
              Abreviatura: vehicle.pickupStore.Abreviatura,
              Sigla: vehicle.pickupStore.Sigla
            },
            cor: {
              CorID: vehicle.color.CorID,
              Cor: vehicle.color.Cor,
              Hexadecimal: vehicle.color.Hexadecimal
            },
            placa: {
              FrotaID: vehicle.plate.info.FrotaID,
              Placa: vehicle.plate.info.Placa,
              StatusID: vehicle.plate.info.StatusID
            },
            protecao: {
              sigla: vehicle.protection.sigla,
              nome: vehicle.protection.nome,
              descricao: vehicle.protection.descricao,
              valor: vehicle.protection.valor
            },
            periodo: {
              meses: vehicle.period.meses,
              franquia: vehicle.mileage.valor.valor,
              sigla: vehicle.mileage.valor.sigla
            },
            on_demand: true
          }
          const token = Crypter.encrypter(data);
          this.sellerS.createSellerLink(token).subscribe(resToken => {
            this.shared.setCounterLoading(false);
            this.router.navigate([`/vendedor/link/${resToken.data.hash}`]);
            return;
          })
        } else {
          this.shared.setCounterLoading(false);
          const msg = 'No foi possvel gerar o link. Tente novamente mais tarde.';
          this.alertMsg.openDialog({
            tipo: 'message',
            titulo: 'Algo deu errado!',
            mensagem: msg
          }, 'message', true);
        }
      }, ex => {
        this.shared.setCounterLoading(false);
        const msg = 'No foi possvel gerar o link. Tente novamente mais tarde.';
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Algo deu errado!',
          mensagem: msg
        }, 'message', true);
      });
    }
  }

  createOrderOnDemand() {
    let utmData = this.defaultUtm;
    if (this.localStorage.get('utm')) {
      utmData = JSON.parse(this.localStorage.get('utm'));
    }
    this.orderS.createOnDemand(this.shared.onDemand, this.shared.vehicleDetails, this.shared.tokenVendedor, utmData).subscribe(res => {
      if (res !== undefined && res != null && res && res.success) {
        this.gtm.addOrderGTM('order', false, res.data.id, this.shared.vehicleDetails);
        this.shared.setCounterLoading(false);
        this.shared.removeTokenVendedor();
        const msg = 'Sua mensagem foi enviada com sucesso! Entraremos em contato em breve.';
        this.alertMsg.openDialog({
          icon: 'ok-icone.svg',
          tipo: 'message',
          titulo: 'Sucesso!',
          mensagem: msg
        }, 'message', true).then(dialogRef => {
          dialogRef.afterClosed().subscribe(() => {
            this.router.navigate(['/']);
          });
        });;
      } else {
        this.shared.setCounterLoading(false);
        const msg = 'No foi possvel enviar seu pedido. Tente novamente mais tarde.';
        this.alertMsg.openDialog({
          tipo: 'message',
          titulo: 'Algo deu errado!',
          mensagem: msg
        }, 'message', true);
      }
    }, ex => {
      this.shared.setCounterLoading(false);
      const msg = 'No foi possvel enviar seu pedido. Tente novamente mais tarde.';
      this.alertMsg.openDialog({
        tipo: 'message',
        titulo: 'Algo deu errado!',
        mensagem: msg
      }, 'message', true);
    });
  }
}
