import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MainSharedService } from 'src/app/util/shared.service';

@Component({
  selector: 'app-plate',
  templateUrl: './plate.component.html',
  styleUrls: ['./plate.component.scss']
})
export class PlateComponent implements OnInit {

  isMobile: boolean = false;
  showPlateDetails: boolean = false;

  public plates: any = [
    { id: 1, label: 'Segunda-feira', value: '1 ou 2' },
    { id: 2, label: 'Tera-feira', value: '3 ou 4' },
    { id: 3, label: 'Quarta-feira', value: '5 ou 6' },
    { id: 4, label: 'Quinta-feira', value: '7 ou 8' },
    { id: 5, label: 'Sexta-feira', value: '9 ou 0' }
  ];

  @Input() setDefaultValue: boolean = false;

  private _vehiclePlates: any[] = [];
  public get vehiclePlates(): any[] {
    return this._vehiclePlates;
  }
  @Input()
  public set vehiclePlates(value: any[]) {
    this._vehiclePlates = value;
    this.choosenPlate = null;

    if (this.setDefaultValue) {
      this.setPlate(value[0]);
    }
  }

  @Input() isOnDemand: boolean = false;

  @Output()
  selectedValue = new EventEmitter();

  choosenPlate: any;

  constructor(private shared: MainSharedService) { }

  ngOnInit() {
    this.windowResize();
  }

  windowResize() {
    if (window.innerWidth < 576) {
      this.isMobile = true;
    } else {
      this.isMobile = false;
    }
  }

  showDetails() {
    this.showPlateDetails = true;
  }

  hideDetails() {
    this.showPlateDetails = false;
  }

  // checkOptions(options) {
  //   this.vehiclePlates =
  //   [...this.vehiclePlates]
  //     .map(plateInfo => {
  //       plateInfo.skus = options
  //         .filter(item => plateInfo.finals.includes(item.placa))
  //         .reduce((final, current) => {
  //           return [...final, ...current.skus]
  //         }, [])
  //       return plateInfo;
  //     })
  //     .filter(plateInfo => plateInfo.skus.length > 0)
  // }

  setPlate(plate) {
    if (this.isOnDemand && plate === this.choosenPlate) {
      this.choosenPlate = null;
      this.selectedValue.emit(null);
    } else {
      this.shared.selectedPlate = plate;
      this.choosenPlate = plate;
      this.selectedValue.emit(plate);
    }
  }
}
