import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MainSharedService } from 'src/app/util/shared.service';

@Component({
  selector: 'app-mileage',
  templateUrl: './mileage.component.html',
  styleUrls: ['./mileage.component.scss']
})
export class MileageComponent implements OnInit {

  public errorKmNotSelected: boolean = false;
  public franchiseDisabled: boolean = false;

  private _options: Array<any>;
  @Input()
  set options(value: Array<any>) {
    this._options = value;
    if (this.setDefaultValue) {
      this.franchiseDisabled = true
      this.selected.emit(value[0].franquia);
      this.checkOptions(value);
    } else {
      this.checkOptions(value);
    }
  }
  get options(): Array<any> {
    return this._options;
  }

  @Input() setDefaultValue: boolean = false
  @Input() setOnDemand: boolean = false
  @Output() selected = new EventEmitter;
  

  choosenMileage: any;
  selectedIndex: number;

  constructor(
    public shared: MainSharedService
  ) { }

  ngOnInit() {
  }

  checkOptions(options) {
    if (options.length > 0) {
      if(this.setDefaultValue) {
        this.setMileage(options[0]);
      } else {
        const franchise = this.choosenMileage ? options.filter(x => x.meses === this.choosenMileage.meses)[0] : null;
        this.setMileage(franchise ? franchise : options[0]);
      }
    }
  }

  setMileageByIndex(index) {
    this.setMileage(this._options[index]);
  }

  setMileage(mileage) {
    
    if(this.setDefaultValue) {
      this.choosenMileage = mileage.franquia;
      this.selected.emit(mileage.franquia);
    } else {
      this.choosenMileage = mileage;
      this.selected.emit(mileage);

      this.selectedIndex = this.options.findIndex(_mileage => {
        return _mileage.franquia === mileage.franquia;
      });
    }
  }
}
