import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CarDetailService } from 'src/app/core/services/car-detail.service';

@Component({
  selector: 'app-store-affiliated',
  templateUrl: './store.component.html',
  styleUrls: ['./store.component.scss']
})
export class StoreAffiliatedComponent implements OnInit {

  @Input() setDefaultValue: boolean = false;

  private _options: Array<any>;
  public get options(): Array<any> {
    return this._options;
  }
  @Input()
  public set options(value: Array<any>) {
    this._options = value;
    if (this.setDefaultValue) {
      this.mySelected.setValue(value[0].Loja);
      this.mySelected.disable();
      this.setPickupStore(value[0], false);
    }
  }

  @Output() selected = new EventEmitter();
  @Output() isOtherPlace = new EventEmitter();
  @Output() selectedCep = new EventEmitter();

  public formData = new FormGroup({
    filial: new FormControl('', [Validators.required])});

  filteredValues: any
  myplaceHolder: string = "Selecione a loja para retirada"
  otherPlace: string = "Retirar em outra loja"
  mySelected = new FormControl();

  constructor(
    private carDetailService: CarDetailService,
  ) { }

  ngOnInit() {
    this.setSelected()
    this.getVehicle()
  }

  setSelected() {
    this.mySelected.valueChanges.subscribe(newValue => {
      this.filteredValues = this.filterValues(newValue);
    })
  }

  onClickFilteredValues() {
    this.mySelected.setValue('')
  }

  getFilteredValues() {
    this.filteredValues === undefined ? this.mySelected.setValue('') : false
    return this.setSelected()
  }

  filterValues(search: string) {
    return this.options.filter(value =>
      this.carDetailService.replaceAccentedChars(value.Loja).toLowerCase().indexOf(this.carDetailService.replaceAccentedChars(search).toLowerCase()) > -1);
  }

  setPickupStoreByFocusout(event) {
    event === "" ? this.setPickupStore(null, false) : false
  }

  verifyOtherPlace(otherPlace) {
    return otherPlace ? this.isOtherPlace.emit(true) : this.isOtherPlace.emit(false)
  }

  setPickupStore(location, otherPlace) {
    if (location !== null) {
      this.verifyOtherPlace(otherPlace)
      this.selected.emit(location)
    } else {
      this.verifyOtherPlace(otherPlace)
      this.selected.emit(null)
    }
  }

  getVehicle(){
    this.carDetailService.getAffiliatedStore().subscribe(async res =>{
      this.options = res;
    });
  }
}
